import React, { Component } from 'react';
import Input from './Input';
import List from './List';



export class TodoList extends Component {
    constructor(props){
        super(props);
        this.state={
            inpValue:'',
            todos:[
                {title:'今天吃饭了',done:true},//true为未完成
                {title:'今天学习了',done:true},//false为已完成
                {title:'今天睡觉了',done:false},
                {title:'今天跑步了',done:false}
            ]
        }
    }
    handleChange=(e)=>{
        this.setState({inpValue:e.target.value});
    }
    componentDidMount(){
    //    this.props.input.focus();//默认焦点
       let todos = localStorage.getItem('todos');
       if(todos){
           this.setState({
               todos:JSON.parse(todos)
           })
       }
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }

    addTodo=()=>{//添加函数
        if(this.state.inpValue==""){
            return;
        }
        this.setState({
            todos:[
                {
                    title:this.state.inpValue,
                    done:false
                },
                ...this.state.todos
            ]               
        })
        this.state.inpValue='';
    }
    deleteTodo=(idx)=>{//删除函数法一
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));//深拷贝一份
        newTodos.splice(idx,1);//第一个参数为从第几个删，第二个参数为删多少个
        this.setState({
            // todos:newTodos
            todos:this.state.todos.filter((item,index)=> index != idx)
        });
    }
    toggle=(index)=>{//点击√到完成那一块
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }
   renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <li>
                    <input 
                        onClick={()=>this.toggle(idx)}
                        checked={done} 
                        type="checkbox"
                    />
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button onClick={()=>this.deleteTodo(idx)}>删除</button>
                </li>
            }        
        })
        
    }
    render() {
        const{todos,inpValue}=this.state;
        let arr1=todos.filter(item=>!item.done);
        let arr2=todos.filter(item=>item.done);
        return (
            <div className="TodoList">
                <Input 
                    inpValue={inpValue}  
                    todos = {todos}
                    handleChange={this.handleChange} 
                    addTodo = {this.addTodo}
                />
                <List 
                    renderTodos={this.renderTodos}
                    todos = {todos}
                    arr1={this.arr1}
                    arr2={this.arr2}

                />
            </div>
        )
    }
}

export default TodoList;


